.main-screen {
  background-color: $color-navy-blue;
  padding-top: 294px;
  padding-bottom: 278px;
  background-image: url("../img/content/main-screen-bg.jpg");

  @include retina {
    background-image: url("../img/content/main-screen-bg.jpg");
  }

  height: 100%;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @include vp-1023 {
    padding-top: 312px;
    padding-bottom: 270px;

    background-image: url("../img/content/main-screen-tablet-bg.jpg");
    background-position: center 8px;

    @include retina {
      background-image: url("../img/content/main-screen-tablet-bg.jpg");
    }
  }

  @include vp-767 {
    padding-top: 225px;
    padding-bottom: 178px;

    background-image: url("../img/content/main-screen-mobile-bg.jpg");
    background-position: center 8px;

    @include retina {
      background-image: url("../img/content/main-screen-mobile-bg.jpg");
    }
  }
}

.main-screen__wrapper {
  margin-left: 98px;

  @include vp-1023 {
    margin-left: 0;
  }
}

.main-screen__wrapper h1 {
  font-weight: 700;
  font-size: 54px;
  color: $color-alice-blue;
  margin: 0 0 30px 0;
  letter-spacing: 3px;

  @include vp-1023 {
    font-size: 50px;
  }

  @include vp-767 {
    font-size: 36px;
    margin: 0 0 9px 0;
    line-height: 45px;
  }
}

.main-screen__wrapper p {
  font-size: 20px;
  line-height: 24px;
  color: $color-alice-blue;
  margin: 0;
  letter-spacing: 1.6px;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  @include vp-1023 {
    font-size: 18px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 22px;
  }
}
